<template>
    <div class="tab-panel">
        <div class="posters__list">
            <span v-for="(poster, index) in posters" :key="index">
                <img :src="poster" @click="choosePoster(poster)" loading="lazy" />
            </span>
        </div>
        <teleport to="body">
            <div v-if="chosenPoster">
                <div class="posters__big" @click="choosePoster(null)">
                    <img :src="chosenPoster" class="clickable" :class="{popy: chosenPosterImage}" />
                </div>
            </div>
        </teleport>
    </div>
</template>
<script>
import { ref } from 'vue';
export default {
    props: {
        posters: Array,
    },
    setup() {
        const chosenPoster = ref(null);
        const chosenPosterImage = ref(false);
        const busy = ref(false);

        function choosePoster(src) {
            if (src !== null) {
                src = src.replace(/_cache\//, "");
            }
            if (this.chosenPoster !== src) {
                if (src === null) {
                    document.body.classList.remove("no-scroll");
                    chosenPosterImage.value = false;
                    setTimeout(() => chosenPoster.value = null, 350);
                } else {
                    chosenPoster.value = src;
                    document.body.classList.add("no-scroll");
                    setTimeout(() => chosenPosterImage.value = true, 50);

                }
            }
        }

        return {
            chosenPoster, chosenPosterImage, busy, choosePoster,
        }
    },
    // computed: {
    //     topPosters() {
    //         return this.posters.slice(0, this.loadedPosters);
    //     },
    // },
    // methods: {

    //     handleScroll: function() {
    //         if (this.chosenPoster || this.busy) return;
    //         // this.scrollPos = document.body.scrollHeight - window.innerHeight - document.body.scrollTop;
    //         const res =
    //             document.body.scrollHeight -
    //             window.innerHeight -
    //             window.scrollY;
    //         //   const res = window.document.height - window.outerHeight - window.scrollY;
    //         console.log(res);
    //         if (res <= 2) {
    //             if (this.posters.length <= this.loadedPosters) {
    //                 console.log("Enough posters loaded");
    //                 return;
    //             }
    //             this.loadedPosters += 4;
    //             this.busy = true; // Tiny throttling to prevent double triggering
    //             console.log("Scrolled to bottom, loaded posters = " + this.loadedPosters);
    //             setTimeout(() => (this.busy = false), 250);
    //         }
    //     },
    // },
    // watch: {
    //     posters() {
    //         // Update search value from parent.
    //         // console.log("Supervalue updated to " + newValue);
    //         this.loadedPosters = 16; // Reset that.
    //     },
    // },
    // mounted() {
    //     // console.log("Created scroll");
    //     window.addEventListener("scroll", this.handleScroll);
    // },
    // unmounted() {
    //     // console.log("Removed scroll");
    //     window.removeEventListener("scroll", this.handleScroll);
    // },
};
</script>
<style>
.posters__list {
    margin: 0 -1rem;
}

.posters__list img {
    display: inline-block;
    margin: 0.6rem 0.7rem;
    border: 1px solid #808080;
    width: 17rem;
    cursor: pointer;
}

.posters__big {
    position: fixed;
    background-color: #ffffff88;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 6000;
}

.posters__big img {
    display: block;
    margin: 3rem auto;
    width: 94vw;
    height: calc(100vh - 6rem);
    object-fit: contain;
    transform: scale(0);
    transition: all 0.3s ease-out;
}

.posters__big > img.popy {
    transform: scale(1);
}

@media screen and (min-width: 768px) {
    .posters__big img {
        max-width: 800px;
    }
}
</style>
